import React from 'react';
import styles from './Button.module.css';
import Icon from "../Icon/Icon";


export default function Button(props) {
    const { name, type, bgColor, color, icon, onClick } = props;

    // 인라인 스타일을 설정합니다.
    const buttonStyle = {
        backgroundColor: bgColor || 'default_bg_color', // bgColor가 전달되지 않은 경우 기본값을 설정하세요.
        color: color || 'default_color', // color가 전달되지 않은 경우 기본값을 설정하세요.
    };

    function handleClick() {
        if (onClick) {
            console.log(onClick);
            onClick();
        }
    }

    return (
        <button className={styles.buttonWrapper} type={type} style={buttonStyle} onClick={handleClick}>
            {icon && <Icon name={icon}/>}
            <div>{name}</div>
        </button>
    );
}
